<template>
  <div class="wrapper">
    <Header @getSearch="search = $event" />
    <Notes :notes="filterNotes" @del="delNote" @edit="editNote" />
    <Modal
      v-show="showModal"
      @showOrClose="closeModal"
      :title="modalTitle"
      :desc="modalDesc"
      :id="curID"
      @changeTitle="modalTitle = $event"
      @changeDesc="modalDesc = $event"
      @addOrChange="addOrChange"
    />

    <a href="#" class="openModal" @click="showModal = true">
      <img src="./assets/img/add-note.svg" alt="" />
    </a>
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import Notes from "./components/Notes";
import Modal from "./components/Modal.vue";
import { v4 as uuidv4 } from "uuid";

export default {
  name: "App",
  components: {
    Header,
    Notes,
    Modal,
  },
  data() {
    return {
      showModal: false,
      modalTitle: "",
      modalDesc: "",
      notes: [],
      search: "",
      curID: null,
    };
  },
  computed: {
    filterNotes() {
      return this.search
        ? this.notes.filter((item) =>
            item.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.notes;
    },
  },
  methods: {
    closeModal(val) {
      if (val == "close") {
        setTimeout(() => {
          this.modalTitle = "";
          this.modalDesc = "";
          this.curID = null;
        }, 400);
        this.showModal = !this.showModal;
      }
    },
    addOrChange(id) {
      let obj = {
        title: this.modalTitle,
        desc: this.modalDesc,
        date: new Date(Date.now()).toLocaleString(),
        id: id || uuidv4(),
      };
      if (id) {
        // console.log(id);
        let idx = this.notes.findIndex((item) => item.id == id);
        this.notes[idx] = obj;
      } else {
        // console.log(id);
        this.notes.push(obj);
      }
      this.curID = null;
      this.closeModal("close");
      this.search = "";
      console.log(inp);
      inp.value = "";
    },
    delNote(id) {
      // console.log(id);
      let idx = this.notes.findIndex((item) => item.id == id);
      // console.log(idx);
      this.notes.splice(idx, 1);
    },
    editNote(id) {
      // console.log(id);
      const obj = this.notes.find((item) => item.id == id);
      // console.log(obj);
      this.modalTitle = obj.title;
      this.modalDesc = obj.desc;
      this.curID = obj.id;
      this.showModal = true;
    },
  },
  mounted() {
    console.log("Mounted");
    // console.log(this.$refs.input);
    // window.addEventListener("scroll", function () {
    //   console.log(this.scrollY);
    // })
  },
  created() {
    console.log('Created');
  },
  updated() {
    console.log('Uptated');
  }
};
</script>

